export var testVertexShader = "\n    void main() {\n        vec4 mvPosition = modelViewMatrix * vec4( position, 1.0 );\n        gl_Position = projectionMatrix * mvPosition;\n    }\n";
export var testFragmentShader = "\n    uniform vec3 iResolution; //viewport resolution(in pixels) \n    uniform float iTime; //shader playback time (in seconds)\n    uniform vec4 iMouse;\n\n    precision highp float;\n\n\n    float gTime = 0.;\n    const float REPEAT = 5.0;\n\n    // \u56DE\u8EE2\u884C\u5217\n    mat2 rot(float a) {\n        float c = cos(a), s = sin(a);\n        return mat2(c,s,-s,c);\n    }\n\n    float sdBox( vec3 p, vec3 b )\n    {\n        vec3 q = abs(p) - b;\n        return length(max(q,0.0)) + min(max(q.x,max(q.y,q.z)),0.0);\n    }\n\n    float box(vec3 pos, float scale) {\n        pos *= scale;\n        float base = sdBox(pos, vec3(.4,.4,.1)) /1.5;\n        pos.xy *= 5.;\n        pos.y -= 3.5;\n        pos.xy *= rot(.75);\n        float result = -base;\n        return result;\n    }\n\n    float box_set(vec3 pos, float iTime) {\n        vec3 pos_origin = pos;\n        pos = pos_origin;\n        pos .y += sin(gTime * 0.4) * 2.5;\n        pos.xy *=   rot(.8);\n        float box1 = box(pos,2. - abs(sin(gTime * 0.4)) * 1.5);\n        pos = pos_origin;\n        pos .y -=sin(gTime * 0.4) * 2.5;\n        pos.xy *=   rot(.8);\n        float box2 = box(pos,2. - abs(sin(gTime * 0.4)) * 1.5);\n        pos = pos_origin;\n        pos .x +=sin(gTime * 0.4) * 2.5;\n        pos.xy *=   rot(.8);\n        float box3 = box(pos,2. - abs(sin(gTime * 0.4)) * 1.5);\t\n        pos = pos_origin;\n        pos .x -=sin(gTime * 0.4) * 2.5;\n        pos.xy *=   rot(.8);\n        float box4 = box(pos,2. - abs(sin(gTime * 0.4)) * 1.5);\t\n        pos = pos_origin;\n        pos.xy *=   rot(.8);\n        float box5 = box(pos,.5) * 6.;\t\n        pos = pos_origin;\n        float box6 = box(pos,.5) * 6.;\t\n        float result = max(max(max(max(max(box1,box2),box3),box4),box5),box6);\n        return result;\n    }\n\n    float map(vec3 pos, float iTime) {\n        vec3 pos_origin = pos;\n        float box_set1 = box_set(pos, iTime);\n\n        return box_set1;\n    }\n\n\n    void main() {\n        vec2 p = (gl_FragCoord.xy * 2. - iResolution.xy) / min(iResolution.x, iResolution.y);\n        vec3 ro = vec3(0., -0.2 ,iTime * 4.);\n        vec3 ray = normalize(vec3(p, 1.5));\n        ray.xy = ray.xy * rot(sin(iTime * .03) * 5.);\n        ray.yz = ray.yz * rot(sin(iTime * .05) * .2);\n        float t = 0.1;\n        vec3 col = vec3(0.);\n        float ac = 0.0;\n\n\n        for (int i = 0; i < 99; i++){\n            vec3 pos = ro + ray * t;\n            pos = mod(pos-2., 4.) -2.;\n            gTime = iTime -float(i) * 0.01;\n            \n            float d = map(pos, iTime);\n\n            d = max(abs(d), 0.01);\n            ac += exp(-d*23.);\n\n            t += d* 0.55;\n        }\n\n        col = vec3(ac * 0.02);\n\n        col +=vec3(0.,0.2 * abs(sin(iTime)),0.5 + sin(iTime) * 0.2);\n\n\n        gl_FragColor = vec4(col ,1.0 - t * (0.02 + 0.02 * sin (iTime)));\n    }\n";
